import { Component } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FormsModule } from '@angular/forms'
import {
  IonContent,
  IonHeader,
  IonTitle,
  IonToolbar,
} from '@ionic/angular/standalone'
import { Router } from '@angular/router'
import { TranslateModule } from '@ngx-translate/core'
import { ConfettiComponent } from 'src/app/components/confetti/confetti.component'
import { AnimationOptions, LottieComponent } from 'ngx-lottie'

@Component({
  selector: 'app-recommandation',
  templateUrl: './recommandation.page.html',
  styleUrls: ['./recommandation.page.scss'],
  imports: [
    IonContent,
    CommonModule,
    FormsModule,
    TranslateModule,
    ConfettiComponent,
    LottieComponent,
  ],
})
export class RecommandationPage {
  options: AnimationOptions = {
    path: '/assets/icon/animated/thumb.json',
  }
  constructor(private router: Router) {}

  async ionViewWillEnter() {
    setTimeout(() => {
      this.router.navigate(['/map'], { replaceUrl: true })
    }, 5000)
  }
}
